export default {
	name: "tuiButton",
	props: {
		// primary, white, danger, warning, green, gray,gradual
		type: {
			type: String,
			default: 'gradual'
		},
		// block, mini, small
		size: {
			type: String,
			default: 'block'
		},
		// circle, square，rightAngle
		shape: {
			type: String,
			default: 'square'
		},
		plain: {
			type: Boolean,
			default: false
		},
		disabled: {
			type: Boolean,
			default: false
		},
		loading: {
			type: Boolean,
			default: false
		},
		openType: {
			type: String,
			default: ''
		},
		formType: {
			type: String,
			default: ''
		},
		hidden: {
			type: Boolean,
			default: false
		},
		bottom: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		handleClick() {
			if (this.disabled) {
				return false;
			}
			this.$emit('click', {})
		},
		bindgetuserinfo({
			detail = {}
		} = {}) {
			this.$emit('getuserinfo', detail);
		},
		getDisabledClass: function(disabled, type) {
			let className = '';
			if (disabled && type != 'white' && type != 'gray') {
				className = type == 'gradual' ? 'btn-gradual-disabled' : 'tui-dark-disabled';
			}
			return className;
		},
		getShapeClass: function(shape, plain) {
			let className = '';
			if (shape == 'circle') {
				className = plain ? 'tui-outline-fillet' : 'tui-fillet';
			} else if (shape == "rightAngle") {
				className = plain ? 'tui-outline-rightAngle' : 'tui-rightAngle';
			}
			return className;
		},
		getHoverClass: function(disabled, type, plain) {
			let className = '';
			if (!disabled) {
				className = plain ? 'tui-outline-hover' : ('tui-' + (type || 'gradual') + '-hover');
			}
			return className;
		}
	}
}
